.navbar-toggler {
  color: var(--color-texts-opacity) !important;
  border-color: var(--color-texts-opacity) !important;
}
.hamburger-icon {
  border-radius: 5px;
  border-width: 2px;
  padding: 3px 10px;
}
.hamburger-icon .hamburger {
  font: inherit;
  display: inline-block;
  overflow: visible;
  margin: 0;
  padding: 2px 0 0;
  cursor: pointer;
  transition-timing-function: linear;
  transition-duration: 0.15s;
  transition-property: opacity, -webkit-filter;
  transition-property: opacity, filter;
  transition-property: opacity, filter, -webkit-filter;
  text-transform: none;
  color: inherit;
  border: 0;
  background-color: transparent;
}
.hamburger-icon .hamburger .hamburger-box {
  position: relative;
  display: inline-block;
  font-size: 23px;
  height: 23px;
  width: 23px;
}
.hamburger-icon .hamburger .hamburger-box i:first-child {
  display: block;
}
.hamburger-icon .hamburger .hamburger-box i:last-child {
  display: none;
}
.hamburger-icon[aria-expanded="true"] .hamburger .hamburger-box i:first-child {
  display: none;
}
.hamburger-icon[aria-expanded="true"] .hamburger .hamburger-box i:last-child {
  display: block;
}
.hamburger-icon:focus {
  outline: 0;
  box-shadow: none;
}
.offcanvas-btn-close {
  position: absolute;
  top: 0;
  right: 0;
  font-size: 22px;
  width: 60px;
  height: 50px;
  display: flex;
  justify-content: center;
  border: 0;
  background: transparent;
  font-weight: 700;
}
.offcanvas-btn-close i {
  color: var(--color-texts) !important;
}
.coodiv-cross-icon {
  transition-timing-function: cubic-bezier(0.55, 0.055, 0.675, 0.19);
  transition-duration: 75ms !important;
  position: absolute;
  transition-timing-function: ease;
  transition-duration: 0.15s;
  transition-property: transform;
  border-radius: 4px;
  background-color: var(--color-texts-opacity);
  top: 50%;
  display: block;
  margin-top: -2px;
  height: 0;
  width: 0;
  left: 50%;
  transform: translateX(-6.5px) rotate(45deg);
}
.coodiv-cross-icon:before {
  top: 0;
  transition: top 75ms ease 0.12s, opacity 75ms ease;
}
.coodiv-cross-icon:after {
  bottom: -3px;
  transition: bottom 75ms ease 0.12s,
    transform 75ms cubic-bezier(0.55, 0.055, 0.675, 0.19);
  transform: rotate(-90deg);
}
.coodiv-cross-icon:after,
.coodiv-cross-icon:before {
  display: block;
  content: "";
  position: absolute;
  width: 20px;
  height: 3px;
  border-radius: 4px;
  background-color: var(--color-texts-opacity);
}
